import { Component, Input, OnInit } from '@angular/core';
import { FilterDateInterface, Sort } from '@nowbrains/ngx-table';

import { CoreMatTableComponent } from '../../../extend/coreMatTableComponent';
import { DateAdapter } from '@angular/material/core';

@Component({
  selector: 'lib-onboarding-table',
  templateUrl: './onboarding-table.component.html',
  styleUrls: ['./onboarding-table.component.scss']
})
export class OnboardingTableComponent extends CoreMatTableComponent {
  @Input() _loading_table;
  @Input() lang;
  @Input() locale: any = {};
  @Input() data: any[] = [];
  @Input() filterParamsResult: any = {}
  @Input() searchInput: string = '';
  @Input() sortInit: Sort = {
    active: 'Id',
    direction: 'desc'
  };
  @Input() rangeInit!: FilterDateInterface;
  @Input() size = 15
  @Input() filter = {}
  @Input() columnDefinitions: any = []
  @Input() startPage = 0

  constructor(
    public dateAdapter: DateAdapter<any>
  ) {
    super(dateAdapter)
    this.apply(() => {
      return this.data
    })
  }

  async ngOnInit() {
    super.ngOnInit();
  }
}
