import { CoreMatTable, CoreMatTableInterface, FilterDateInterface, Sort } from '@nowbrains/ngx-table';
import { Injectable, Input, OnInit, SimpleChanges } from "@angular/core";

import { DateAdapter } from '@angular/material/core';

@Injectable()
export class CoreMatTableComponent implements OnInit {
  @Input() data: any[] = [];
  @Input() filterParamsResult: any = {}
  @Input() searchInput: string = '';
  @Input() sortInit!: Sort;
  @Input() rangeInit!: FilterDateInterface;
  @Input() size = 15
  @Input() filter = {}
  @Input() startPage = 0
  @Input() columnDefinitions: any[] = []
  mapData!: () => any
  lang = "fr"
  public _data: CoreMatTableInterface | undefined;
  private _currentDate: any;
  private _currentFilter: any;
  constructor(
    public dateAdapter: DateAdapter<any>) {

  }


  ngOnInit(): void {
    this._data = new CoreMatTable(this.data ? this.data : [], this.sortInit, this.rangeInit, this.size, true, true, this.filter, this.startPage)
  }


  ngOnChanges(changes: SimpleChanges): void {
    this.triggerChanges(changes)
  }


  private triggerChanges(changes: SimpleChanges): void {
    if (this.data.length >= 0) {
      const x = this.mapData();
      if (this._data && changes.data) this._data.setData(x)
      if (this._data) {
        if (changes.searchInput) this._data.search.next(changes.searchInput?.currentValue)
        if (changes.filterParamsResult) {
          const { filterDate, filterData } = this.filterParamsResult;
          if (filterDate && filterDate !== this._currentDate) {
            this._currentDate = filterDate
            this._data.filterDate(filterDate)
          }
          if (filterData && filterData !== this._currentFilter) {
            this._currentFilter = filterData;
            this._data.filter(filterData)
          }
        }
      }
    } else if (this._data) {
      this._data.setData([])
    }
  }

  apply(map: () => any) {
    this.mapData = map
  }
}
