import { Component, Input, OnChanges, OnInit, SimpleChanges } from '@angular/core';
import { animate, state, style, transition, trigger } from "@angular/animations";
import { AnimationItem } from "ngx-lottie/lib/symbols";
import { AnimationOptions } from "ngx-lottie";

export const FadeInOut = trigger('fadeInOut', [
  state(
    'loaded',
    style({
      opacity: 0,
      'z-index': -1
    })
  ),
  state(
    'loading',
    style({
      opacity: 1,
    })
  ),
  transition('* => *', animate('500ms ease-out')),
])

@Component({
  selector: 'nowboard-loader',
  templateUrl: './nowboard-loader.component.html',
  styleUrls: ['./nowboard-loader.component.scss'],
  animations: [
    FadeInOut
  ]
})
export class NowboardLoaderComponent implements OnInit, OnChanges {
  @Input() state: boolean = true;
  public _state: 'loaded' | 'loading' = 'loading';
  options: AnimationOptions = {
    path: '/assets/loader/light.json',
  };

  constructor() {
  }

  set State(value: 'loaded' | 'loading') {
    this._state = value
  }

  animationCreated(animationItem: AnimationItem): void {
  }

  ngOnInit(): void {

  }

  ngOnChanges(changes: SimpleChanges) {
    if (changes && changes.state !== undefined) {
      this.State = changes.state.currentValue ? 'loading' : 'loaded';
    }
  }

}
