import { Component, EventEmitter, HostListener, Input, OnInit, Output } from '@angular/core';
import { MatBottomSheet, MatBottomSheetRef } from '@angular/material/bottom-sheet';

import { OnboardingService } from '../../../onboarding.service';
import { Router } from '@angular/router';
import { locales } from '../../../i18n';

@Component({
  selector: 'lib-onboarding-detail',
  templateUrl: './onboarding-detail.component.html',
  styleUrls: ['./onboarding-detail.component.scss']
})
export class OnboardingDetailComponent implements OnInit {
  @Input() detailView;
  @Output() onShowMode = new EventEmitter<boolean>(true);
  locale: any = {}
  constructor(private router: Router, private service: OnboardingService, private _bottomSheet: MatBottomSheet) {
    this.service._lang.subscribe((locale) => {
      this.locale = locales[locale].REQUEST;
    })
  }
  ngOnInit(): void {
  }

  openBottomSheet(attach): void {
    this._bottomSheet.open(BottomSheet, {
      data: attach
      // data: Attachement && Attachement.length > 1 ? Attachement.find((attach: any) => attach.Name.includes("Configuration")) : Attachement[0]
    });
  }
  @HostListener('scroll', ['$event'])
  onScroll(event) {
    const sections = document.querySelectorAll("section");
    const navLi = document.querySelectorAll(".fields ul li");

    let current = "";
    sections.forEach((section) => {
      const sectionTop = section.offsetTop;
      if (event.target.scrollTop > sectionTop - 300) {
        current = section.getAttribute("id")
      }
    });
    navLi.forEach((li) => {
      const id = li.getAttribute('id')
      if (id?.substring(0, id.length - 1) === current) {
        li.classList.add("active");
      } else {
        li.classList.remove('active')
      }
    });
  }

  catNameStart(str: string) {
    const vowels = ['a', 'e', 'i', 'o', 'u', 'y'];
    return vowels.includes(str.substr(0, 1).toLocaleLowerCase())
  }
  onEdit() {
    this.onShowMode.emit(false)
  }

  progress(completed, length) {
    return 100 * completed / length
  }

  substr(name: string) {
    return name.length > 30 ? `${name.slice(0, 15)}...${name.slice(-15)}` : name
  }

  onViewTicket(Case) {
    this.router.navigate([`reporting/dashboard/${Case._id}`])
  }
  onFormatDisplayValue(value) {
    if (Array.isArray(value)) {
      return {
        type: 'array',
        value
      }
    }
    return value === true ? { value: "Oui", type: 'string' } : value === false ? { value: "Non", type: 'string' } : { value, type: 'string' }

  }

}


@Component({
  selector: 'bottom-sheet',
  templateUrl: 'bottom-sheet.html',
  styleUrls: ['./onboarding-detail.component.scss']
})
export class BottomSheet implements OnInit {
  attach = null;
  filename = ""
  not_found = ""
  locale: any = {}
  constructor(private _bottomSheetRef: MatBottomSheetRef<BottomSheet>, private service: OnboardingService) {
    this.service._lang.subscribe((locale) => {
      this.locale = locales[locale].REQUEST;
    })

  }
  ngOnInit(): void {
    this.attach = this._bottomSheetRef.containerInstance._config.data;
  }

  close(): void {
    this._bottomSheetRef.dismiss();
  }

  async onDownloadFile() {
    if (this.attach) {
      const result = await this.service.downloadAttachment(this.attach);
      if (!result) {
        this.not_found = "fichier non trouvé"
        setTimeout(() => {
          this._bottomSheetRef.dismiss();
          this.not_found = ""
        }, 3000);
      }

    }
  }
}

