import { Component, OnInit, Inject } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { locales } from '../../../i18n';
import { OnboardingService } from '../../../onboarding.service';

@Component({
  selector: 'lib-type',
  templateUrl: './type.component.html',
  styleUrls: ['./type.component.scss']
})
export class TypeComponent implements OnInit {
  typeList = [{
    label: 'SHORT_TEXT',
    icon: 'list_alt',
    value: 'text'
  },
  {
    label: 'LONG_TEXT',
    icon: 'description',
    value: 'textarea'
  },
  {
    label: 'SINGLE_CHOICE_OPTION',
    icon: 'radio_button_checked',
    value: 'radio'
  },
  {
    label: 'OFF_ON_BUTTON',
    icon: 'check_box_outline_blank',
    value: 'toggle'
  },
  {
    label: 'CHECKBOX',
    icon: 'check_box',
    value: 'checkbox'
  },
  {
    label: 'MULTIPLE_CHECKBOX',
    icon: 'check_box',
    value: 'checkbox_multiple'
  },
  {
    label: 'DROPDOWN_LIST',
    icon: 'keyboard_arrow_down',
    value: 'select'
  },
  {
    label: 'EMAIL',
    icon: 'mail',
    value: 'email'
  },
  {
    label: 'PASSWORD',
    icon: 'lock',
    value: 'password'
  },
  {
    label: 'PHONE',
    icon: 'phone',
    value: 'tel'
  },
  {
    label: 'DATE',
    icon: 'date_range',
    value: 'date'
  },
  {
    label: 'NONE',
    icon: 'square',
    value: 'none'
  }]
  selectedType = ""
  locale: any = {}
  constructor(@Inject(MAT_DIALOG_DATA) private data: any,
    private dialogRef: MatDialogRef<TypeComponent>, private service: OnboardingService) {
    this.selectedType = this.data.selectedType;
    this.service._lang.subscribe((locale) => {
      this.locale = locales[locale].PROFILE;
    })
  }

  ngOnInit(): void {
  }

  onSelect(type): void {
    this.dialogRef.close(type);
  }

}
